
#app {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  width: 100vw;
  height: 100vh;
}
header {
  width: 100%;
}
header > h1 {
  position: relative;
  margin: 0 auto;
  text-align: center;
  color: rgb(14, 84, 97);
  font-size: 50px;
}

#login {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
#box {
  position: relative;
  height: 300px;
  width: 300px;
  margin: 0 auto;
  top: 50%;
  -webkit-transform: translateY(-80%);
          transform: translateY(-80%);
  -webkit-box-shadow: 0 0 5px gray;
          box-shadow: 0 0 5px gray;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  text-align: center;
  font-size: 18px;
  padding: 50px;
}
#box > button {
  position: relative;
  border: none;
  background: lightskyblue;
  height: 40px;
  width: 80px;
  margin: 0 auto;
  font-size: 20px;
  border-radius: 5px;
  color: white;
  margin-top: 20px;
}
#box > button:hover {
  background: rgb(5, 82, 107);
}
